.blog {
}

.wrap__container {
   margin-right: -16px;
   margin-left: -16px;

   @include tablet {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between
   }
}
.blog-subtitles {

   @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
   }
   @include desktop {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      //width: 692px;
   }
}

.blog__texts {
   @include desktop {
      width: 693px;
   }
}

.blog__description {
   @include desktop {
      display: flex;
      flex-direction: column;
      margin-left: 40px;
   }
}

.blog__titless {

}
.blog__titles-one {
         @include desktop {
            display: none;
         }
}

.blog__titles-two {
   display: none;
   @include desktop {
      display: block;
      width: 692px;
   }
}

.blog__tit {
   @include desktop {
      display: flex;
      flex-direction: row-reverse;
      justify-content: flex-start;
   }
}

