.blog__container {
         background-repeat: no-repeat;
         background-position: center;
         background-size: cover;
         background-image: url('../images/blog-part/img_blog_bg_t_1x.jpg');
      
         @media (min-device-pixel-ratio: 2),
         (-webkit-min-device-pixel-ratio: 2),
         (min-resolution: 192dpi),
         (min-resolution: 2dppx) {
            background-image: url('../images/blog-part/img_blog_bg_t_2x.jpg');
         }
      
         @media screen and (min-width: 1280px) {
            background-image: url('../images/blog-part/img_blog_bg_d_1x.jpg');
      
            @media (min-device-pixel-ratio: 2),
            (-webkit-min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
               background-image: url('../images/blog-part/img_blog_bg_d_2x.jpg');
            }
         }
}

.blog__title-first {
   margin-bottom: 31px;
   font-weight: 500;
   color: #FFFFFF;

   @include tablet {
      margin-bottom: 75px;
      font-size: 56px;
      line-height: 1.18;
   }
}

.blog__title {
   margin-bottom: 16px;
   font-weight: 500;
   line-height: 1.18;
   color: #FFFFFF;

   @include tablet {
      font-size: 56px;
   }
   @include desktop {
      margin-bottom: 52px;
   }
}

.blog__title-a {
   font-weight: 500;
   line-height: 1.18;
   color: #FFFFFF;

   @include tablet {
      margin-bottom: 24px;
      font-size: 56px;
   }
}

.blog__article {
   padding-bottom: 40px;

   &:last-child {
      padding-bottom: 48px;
   }
}

.blog__img {
   @include tablet {
      width: 387px;
      border-radius: 12px;
   }
}

.blog__subtitle {
   font-weight: 500;
   font-size: 24px;
   line-height: 1.16;
   color: #FFFFFF;
   margin-bottom: 24px;
}

.blog__link {
   margin-left: auto;
   font-weight: 500;
   font-size: 16px;
   line-height: 1;
   color: #86E543;

   @include tablet {
      margin-top: 16px;
      font-size: 24px;
   }
}

.blog__text {
   font-size: 16px;
   line-height: 1.18;
   color: #FFFFFF;

   @include tablet {
   font-size: 24px;
   line-height: 1.16;
   }
}

.blog__subtext {
   font-weight: 500;
   font-size: 16px;
   line-height: 1;
   color: #8D9289;

   @include tablet {
      margin-bottom: 24px;
      font-size: 24px;
   }
}